import React from 'react';
import style from './Fotter.module.scss';
import { SocialIcon } from 'react-social-icons';

const Fotter = () => {
  return (
    <div className={style.fotter}>
      <div className={style.phoneAndAdress}>
        <h2>тел.факс (044) 454-45-73</h2>
        <h3>
          c_support_zsu@post.mil.gov.ua
          <br />
          03168, м. Київ, пр-кт Повітрофлотський, 6
        </h3>
      </div>
      <div className={style.LeadersContacts}>
        <a target='_blank' href='https://www.president.gov.ua/en'>
          Президент України
        </a>
        <br />
        <a target='_blank' href='https://www.rada.gov.ua/'>
          Верховна Рада України
        </a>
        <br />
        <a target='_blank' href='https://www.mil.gov.ua/'>
          Міністерство оборони України
        </a>
        <br />
        <a target='_blank' href='https://mva.gov.ua/ua'>
          Міністерство у справах ветеранів України
        </a>
      </div>
      <div className={style.FaceBookPagesOfArmies}>
        <a target='_blank' href='https://www.facebook.com/UkrainianLandForces/'>
          Сухопутні війська
        </a>
        <br />
        <a target='_blank' href='https://www.facebook.com/kpszsu/'>
          Повітряні Сили{' '}
        </a>
        <br />
        <a target='_blank' href='https://navy.mil.gov.ua/'>
          Військово-Морські Сили
        </a>
        <br />
        <a target='_blank' href='https://www.dshv.mil.gov.ua/istoriya'>
          Десантно-штурмові війська
        </a>
        <br />
        <a target='_blank' href='http://sof.mil.gov.ua/join'>
          Сили спеціальних операцій
        </a>
        <br />
        <a target='_blank' href='https://www.facebook.com/UA.Military.Police/'>
          Військова служба правопорядку
        </a>
      </div>
      <div className={style.MainSocialMedias}>
        <SocialIcon
          target='_blank'
          url='https://www.facebook.com/GeneralStaff.ua'
          style={{ height: '20px', width: '20px' }}
          bgColor={'#fff'}
        />
        <SocialIcon
          target='_blank'
          url='https://mobile.twitter.com/GeneralStaffUA'
          style={{ height: '20px', width: '20px', marginLeft: '10px' }}
          bgColor={'#fff'}
        />
        <SocialIcon
          target='_blank'
          url='https://www.youtube.com/channel/UC10u3bDYe1HMHOQLvufXqjA'
          style={{ height: '20px', width: '20px', marginLeft: '10px' }}
          bgColor={'#fff'}
        />
        <SocialIcon
          target='_blank'
          url='https://www.instagram.com/uaf_general_staff/'
          style={{ height: '20px', width: '20px', marginLeft: '10px' }}
          bgColor={'#fff'}
        />
      </div>
      <div className={style.SiteMap}>Карта сайту</div>
      <div className={style.Copyright}>©2021 Збройні Сили України</div>
    </div>
  );
};

export default Fotter;
